/* GLOBAL THEME */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #d0eaff, #f3e8ff);
  min-height: 100vh;
}

/* CONTAINER */
.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 25px;
  text-align: center;
}

/* NAVBAR */
.navbar {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 15px;
  margin-bottom: 40px;
  background: white;
  border-radius: 12px;
  box-shadow: 0px 4px 12px rgba(0,0,0,0.1);
}

.nav-btn {
  padding: 12px 22px;
  border-radius: 10px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  transition: 0.3s;
  background: #007bff;
  color: white;
  font-weight: 500;
}

.nav-btn:hover {
  transform: translateY(-3px);
  background: #005ad3;
}

/* SECTION LIST */
.section-list p {
  font-size: 18px;
  padding: 12px;
  margin: 10px auto;
  width: 80%;
  border-radius: 10px;
  background: white;
  cursor: pointer;
  transition: 0.25s;
  box-shadow: 0px 4px 8px rgba(0,0,0,0.08);
}

.section-list p:hover {
  background: #e1f0ff;
  transform: translateY(-4px);
}

/* HEADINGS */
h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #222;
}
@keyframes glowBorder {
  0% { box-shadow: 0 0 25px rgba(0,255,200,0.4); }
  50% { box-shadow: 0 0 45px rgba(0,255,200,0.9); }
  100% { box-shadow: 0 0 25px rgba(0,255,200,0.4); }
}

.animate-glowBorder {
  animation: glowBorder 3s infinite ease-in-out;
}